<template>
  <div class="page">
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="admin-breadcrumb">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>{{title}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="headings">{{title}}</div>
    <div class="listBox">
      <!-- 按钮框 -->
      <div class="listBtnBox" style="float:left">
        <el-button type="danger" @click="del">删除</el-button>
      </div>
      <!-- 搜索 -->
      <el-form :inline="true" class="demo-form-inline" style="float:right;height:55px">
        <el-form-item label="问题">
          <el-input v-model="keyword" placeholder="问题关键字"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getList">查询</el-button>
        </el-form-item>
      </el-form>
      <!-- 列表 -->
      <el-table ref="listTable" v-loading="loading" :data="list.Items" @selection-change="SelectionChange" style="width: 100%">
        <el-table-column type="selection" width="40" />
        <el-table-column prop="content" label="问题">
          <template slot-scope="scope">
            <div class="title" @click="to(scope.$index, scope.row)">{{ scope.row.content }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="content" label="提问次数" width="200">
        </el-table-column>

        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="to(scope.$index, scope.row)">设置答复</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="listPage">
        <el-pagination @current-change="handleCurrentChange" background layout="total, prev, pager, next, jumper" :total="list.TotalItems" :page-size="list.ItemsPerPage" />
      </div>

    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: this.$route.name,
      loading: true,
      keyword: "",
      list: {
        Items: []
      },
      Selection: []
    };
  },
  mounted: function() {
    this.getList();
  },
  methods: {
    // 分页
    handleCurrentChange(val) {
      this.getList(val);
    },
    // getList
    getList(curr) {
      this.Selection = [];
      this.loading = true;
      this.$http
        .get(this.httpUrl + "/Record/Index", {
          params: { page: curr, Content: this.keyword }
        })
        .then(response => {
          this.list = response.data;
          this.loading = false;
        });
    },
    // 跳转
    to(index, row) {
      this.$router.push({
        path: "/problem/add?countID=" + this.list.Items[index].id+"&countTitle="+this.list.Items[index].content
      });
    },
    // 选中
    SelectionChange(val) {
      this.Selection = [];
      for (let index = 0; index < val.length; index++) {
        this.Selection[index] = val[index].id;
      }
    },
    // 删除
    del() {
       if (this.Selection.length!=0) {
        this.$http
          .post(this.httpUrl + "/Record/Delete", this.Selection, {
            headers: this.postHeader
          })
          .then(response => {
            if (response.data.status == "y") {
              this.$notify({
                title: '成功',
                message: "删除成功",
                type: "success"
              });
              this.getList();
            } else {
              this.$notify({
                title: '失败',
                message: response.data.info,
                type: "warning"
              });
            }
          });
      }else{
        this.$notify({
          title: '警告',
          message: '您没有勾选任何选项！',
          type: 'warning'
        });
      }
    }
  }
};
</script>

<style>
</style>


